import React from 'react';
import './App.css';


const App = () =>
{

let RGB= [];
let i,j,k;

const R = Array.from({length: 32}, (_, i) => i * 8);
const G = Array.from({length: 32}, (_, i) => i * 8);
const B = Array.from({length: 32}, (_, i) => i * 8);
  
 for(i=0;i<R.length;i++)
 {
  for(j=0;j<G.length;j++)
  {
    for(k=0;k<B.length;k++)
    {
      RGB=[[R[i],G[j],B[k]],...RGB];
    }
    
  }
 }

const slicing = (start,end,name) =>
{

 return  (
     <div className={`wrap ${name}`}>
     {  
      RGB.slice(start,end).map((item,index)=>
        <div onClick={()=>oncolorclick(item)} key={index} className="eachimg" style={{backgroundColor:`rgb(${item})`}}>
        </div>
      )

      }
      </div>
        );

}

const oncolorclick = (colorcode) =>
{
  console.log(`RGB(${colorcode})`);
}


return(  
<div>    
 <h1 className="gradient-text">Colour Generator</h1>
   <div className="container">
     {slicing(0,8192,"first")}
     {slicing(8192,16384,"second")}
     {slicing(16384,24576,"third")}
     {slicing(24576,32768,"fourth")}
   </div>
</div>
  );
}

export default App;
